import React from 'react';
import './theCSS.css';
import { Navbar, Nav, NavDropdown, Container} from 'react-bootstrap';
import {LinkContainer } from 'react-router-bootstrap';

function Toolbar() {

    return (
        <Navbar collapseOnSelect="true" expand="lg" bg="light" >
            <Container>
                <LinkContainer className="mylink" to="/">
                    <Navbar.Brand>Logo</Navbar.Brand>
                </LinkContainer>
                <Navbar.Toggle aria-controls="responsive-navbar-nav" />
                <Navbar.Collapse  id="responsive-navbar-nav">
                <Nav className="ml-auto" > 
                    <LinkContainer className="mylink" to="/">
                        <Nav.Link>Αρχική</Nav.Link>
                    </LinkContainer>
                    <NavDropdown title="Προϊόντα" id="basic-nav-dropdown">
                        <LinkContainer className="mylink" to="/products/bags">
                            <NavDropdown.Item>Ζώνες</NavDropdown.Item>
                        </LinkContainer>
                        <LinkContainer className="mylink" to="/products/bags">
                            <NavDropdown.Item>Φουλάρια</NavDropdown.Item>
                        </LinkContainer>
                        <LinkContainer className="mylink" to="/products/bags">
                            <NavDropdown.Item>Τσάντες</NavDropdown.Item>
                        </LinkContainer>
                    </NavDropdown>
                    <LinkContainer className="mylink" to="/MyMap">
                        <Nav.Link >Βρείτε το κατάστημα</Nav.Link>
                    </LinkContainer>
                    <LinkContainer className="mylink" to="/">
                        <Nav.Link >Σύνδεση</Nav.Link>
                    </LinkContainer>
                </Nav>
                </Navbar.Collapse>
            </Container>
        </Navbar>

  )


}
export default Toolbar