import React from 'react';
import { Container,Row,Col } from 'react-bootstrap';
//
function MyFooter() {

    return (
      <footer sticky="bottom" className="py-2 bg-dark">
          <Container>
            <Row>
              <Col className="text-center py-3">
                  <p className="text-white">Copyright &copy; Your Website 2020</p>
              </Col>
            </Row>
          </Container>
      </footer>
    )
}

export default MyFooter;